function RoundPreloader({ color = 'first', speed = 690, style, children }) {
    const mergedStyles = {
        color,
        WebkitAnimation: `cssload-spin ${speed}ms infinite linear`,
        animation: `cssload-spin ${speed}ms infinite linear`,
        ...style,
    };

    return (
        <div style={mergedStyles} className="loader loader1">
            <span></span>
        </div>
    );
}
